import React, {useEffect} from "react";
import TopBanner from "../../components/top-banner";
import SexBlogCardSection from "../../components/sex-blog-cards-section";
import {useTranslation} from 'react-i18next';
import SexBlogImg from "../../images/sexBlogImg.png";
import SexBlogBg from "../../images/sexBlogBg.png";

import "./index.css";

export default function SexBlog(props) {
    const {t} = useTranslation();

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    const topBannerInfo = {
        header: t("sexBlogBannerHeader"),
        description: t("sexBlogBannerDescription"),
        frontImg: SexBlogImg,
        backImg: SexBlogBg,
        backColor: "#63308F",
    };

    return (
        <>
            <TopBanner topBannerInfo={topBannerInfo}/>
            <section className="sex-blog-section base-padding">
                <div className="base-container">
                    <SexBlogCardSection language={props.language}/>
                </div>
            </section>
        </>
    );
}
