import React, {useState} from "react";
import {Switch, Route, Redirect} from "react-router-dom";
import CoachTests from "../coach-section-tests";
import CoachQuizContainer from "../coach-section-quiz";
import CoachMaterials from "../coach-section-materials";
import CoachMaterialArticle from "../coach-section-material-article";
import CoachManual from "../coach-section-manual";
import CoachAccount from "../coach-section-account";
import CoachNavPanel from "../../components/coach-nav-panel";
import "./index.css";

export default function CoachSection(props) {
    const [isUser] = useState(!!window.localStorage.getItem("user"))
    const {language} = props

    if (isUser)
        return (
            <>
                <CoachNavPanel/>
                <Switch>
                    <Route exact path="/coach" render={(props) => <CoachMaterials {...props} language={language}/>}/>
                    <Route path="/coach/account" component={CoachAccount}/>
                    <Route exact path="/coach/manual"
                           render={(props) => <CoachManual {...props} language={language}/>}/>
                    <Route exact path="/coach/tests" render={(props) => <CoachTests {...props} language={language}/>}/>
                    <Route exact path="/coach/tests/:testId"
                           render={(props) => <CoachQuizContainer {...props} language={language}/>}/>
                    <Route path="/coach/:materialId" component={CoachMaterialArticle}/>
                </Switch>
            </>
        );
    else return <Redirect push to="/coachlogin"/>;
}
