import React, {useEffect, useState} from "react";
import CoachMaterialCard from "../../components/coach-material-card";
import "./index.css";
import API from "../../API";

export default function CoachMaterials(props) {
    const [allCoachMaterials, setAllCoachMaterials] = useState();

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getAllCoachMaterials(props.language);
            setAllCoachMaterials(result.data.blogs);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    return (
        <section className="coach-materials-container base-container bottom-padding grid-2">
            {allCoachMaterials ? allCoachMaterials.map((item, id) => (
                <CoachMaterialCard key={id} cardContent={item}/>
            )) : <></>}
        </section>
    );
}
