import React, {useEffect, useState} from "react";
import {Redirect} from "react-router-dom";
import {useTranslation} from 'react-i18next';
import "./index.css";
import API from "../../API";

export default function CoachAuthorization() {
    const {t,} = useTranslation();
    const [loginInfo, setLoginInfo] = useState({
        phone: "",
        password: ""
    })
    const [error, setError] = useState("no");
    const [redirect, setRedirect] = useState(!!window.localStorage.getItem("user"))

    const handleInputs = (e) => {
        e.persist();
        setLoginInfo((formData) => ({
            ...formData,
            [e.target.name]: e.target.value,
        }));
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        API.postLogin(loginInfo)
            .then((response) => {
                if (response.data.userExists) {
                    window.localStorage.setItem("user", response.data["userExists"]._id)
                    window.localStorage.setItem("currentARZUserPassword", loginInfo.password)
                    setRedirect(true)
                } else if (response.data.result.status === 0) {
                    setError("–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –ø–æ–ª—è!")
                } else {
                    setError(response.data.result.status_description)
                }
            })
    }

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    if (redirect)
        return <Redirect push to="/coach"/>;
    else
        return (
            <section className="coach-section">
                <div className="base-container base-padding flex-col align-center">
                    <h1>{t("coachAuthHeader")}</h1>
                    <h1>{t("coachAuthHeader2")}</h1>
                    <form className="flex-col align-start" onSubmit={handleSubmit}>
                        <label htmlFor="phone">{t("coachAuthLabel1")}</label>
                        <input onChange={handleInputs} id="phone" type="text" name="phone"
                               placeholder="0999 99 99 99"/>
                        <label htmlFor="password">{t("coachAuthLabel2")}</label>
                        <input onChange={handleInputs} id="password" type="password" name="password"
                               placeholder="********"/>
                        <button>{t("coachAuthButton")}</button>
                    </form>
                    <h5 style={{
                        opacity: error === "no" ? "0" : "1",
                        color: "#9b0a00",
                        marginTop: "30px"
                    }}>{error}</h5>
                </div>
            </section>
        );
}
