import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faInstagram, faFacebookF} from "@fortawesome/free-brands-svg-icons";
import {faEnvelope, faPhoneVolume} from "@fortawesome/free-solid-svg-icons";
import {useTranslation} from 'react-i18next';
import footerLogo from "../../images/footerLogo.png";

import "./index.css";
import API from "../../API";

export default function Footer(props) {
    const {t} = useTranslation();
    const [mainPageInfo, setMainPageInfo] = useState({})

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getMainPageInfo();
            setMainPageInfo(result.data.banner);
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    return (
        <footer>
            <div className="footer-container base-container grid-4">
                <img src={footerLogo} alt="–ê–†–ó"/>
                <div className="footer-name">
                    <h2>{t("mainHead2")}</h2>
                    <p>
                        {
                            mainPageInfo ?
                                props.language === "ru" ?
                                    mainPageInfo.content :
                                    props.language === "en" ?
                                        mainPageInfo.contentEN :
                                        mainPageInfo.contentKG :
                                ""
                        }
                    </p>
                </div>
                <div className="footer-links-container">
                    <h3>{t("footerLinks")}</h3>
                    <div className="flex-col footer-links">
                        <Link to="/sexblog">{t("headerSexblog")}</Link>
                        <Link to="/quiz">{t("headerQuiz")}</Link>
                        <Link to="/parentsblog">{t("headerParentsblog")}</Link>
                        <Link to="/stories">{t("headerStories")}</Link>
                        <Link to="/faq">{t("headerFaq")}</Link>
                    </div>
                </div>
                <div className="footer-social-container flex-col align-center">
                    <h3>{t("footerSocial")}</h3>
                    <div className="footer-social">
                        <a href={mainPageInfo.facebook} rel="noopener noreferrer" target="_blank"
                           className="footer-icon">
                            <FontAwesomeIcon icon={faFacebookF}/>
                        </a>
                        <a href={mainPageInfo.instagram} rel="noopener noreferrer" target="_blank"
                           className="footer-icon">
                            <FontAwesomeIcon icon={faInstagram}/>
                        </a>
                        <a href={`tel:${mainPageInfo.phone}`} rel="noopener noreferrer" target="_blank"
                           className="footer-icon footer-phone">
                            <FontAwesomeIcon icon={faPhoneVolume}/>
                        </a>
                        <a href={`mailto:${mainPageInfo.email}`} rel="noopener noreferrer" target="_blank"
                           className="footer-icon">
                            <FontAwesomeIcon icon={faEnvelope}/>
                        </a>
                    </div>
                </div>
            </div>
        </footer>
    );
}
