import React, {useEffect} from "react";
import TopBanner from "../../components/top-banner";
import StoryCardsSection from "../../components/story-cards-section";
import {useTranslation} from 'react-i18next';
import StoriesBlogImg from "../../images/storiesBlogImg.png";
import StoriesBlogBg from "../../images/storiesBlogBg.png";

export default function Stories(props) {
    const {t} = useTranslation();
    useEffect(() => {
        window.scrollTo(0, 0);
    });

    const topBannerInfo = {
        header: t("storiesBannerHeader"),
        description: t("storiesBannerDescription"),
        frontImg: StoriesBlogImg,
        backImg: StoriesBlogBg,
        backColor: "#75BF43",
    };

    return (
        <>
            <TopBanner topBannerInfo={topBannerInfo}/>
            <section>
                <div className="base-container ">
                    <StoryCardsSection language={props.language}/>
                </div>
            </section>
        </>
    );
}
