import React, {useState, useEffect} from "react";
import API from "../../API";
import Loader from "react-loader-spinner";
import SexBlogSmallCard from "../sex-blog-small-card";
import SexBlogBigCard from "../sex-blog-big-card";

import "./index.css";

export default function StoryCardsSection(props) {
    const [sexBlogArticles, setSexBlogArticles] = useState([]);
    const [sexBlogSelectedTag, setSexBlogSelectedTag] = useState(null);
    const [sexBlogTags, setSexBlogTags] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getSexBlogAllArticles(props.language);
            setSexBlogArticles(result.data.blogs);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getAllTags(props.language);
            setSexBlogTags(result.data.tags);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getAllArticlesByTag(sexBlogSelectedTag);
            setSexBlogArticles(result.data.tag.blogs);
        };
        sexBlogSelectedTag && fetchData();
        // eslint-disable-next-line
    }, [sexBlogSelectedTag]);

    return (
        <>
            {setSexBlogArticles.length ? (
                <>
                    <div className="tag-navigation-bar flex-row justify-start">
                        {sexBlogTags.length ? (
                            sexBlogTags.map((item, id) => (
                                <p onClick={(e) =>
                                    setSexBlogSelectedTag(item._id)} key={id}>
                                    {item.title}
                                </p>
                            ))
                        ) : (
                            <></>
                        )}
                    </div>
                    <div className="sex-blog-cards-section">
                        {sexBlogArticles.length ? (
                            sexBlogArticles.map((item, id) =>
                                id % 4 ? (
                                    <SexBlogSmallCard
                                        key={id}
                                        cardContent={{
                                            id: item._id,
                                            name: item.title,
                                            description: item.content,
                                            img: item.photo,
                                        }}
                                    />
                                ) : (
                                    <SexBlogBigCard
                                        key={id}
                                        cardContent={{
                                            id: item._id,
                                            name: item.title,
                                            description: item.content,
                                            img: item.photo,
                                        }}
                                    />
                                )
                            )
                        ) : (
                            <h1>–ù–µ—Ç –±–ª–æ–≥–æ–≤</h1>
                        )}
                    </div>
                </>
            ) : (
                <Loader
                    type="TailSpin"
                    color="#00BFFF"
                    height={50}
                    width={50}
                    style={{margin: "auto"}}
                    timeout={5000}
                />
            )}
        </>
    );
}
