import React, {useState, useEffect} from "react";
import API from "../../API";
import "./index.css";
import {useTranslation} from "react-i18next";
import SanitizedHTML from "react-sanitized-html";

export default function About(props) {
    const [aboutList, setAboutList] = useState([]);
    const {t} = useTranslation()

    useEffect(() => {
        window.scrollTo(0, 0);
        const fetchData = async () => {
            const result = await API.getAboutUsInfo();
            setAboutList(result.data);
            console.log(result.data);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    return (
        <div className="about-section">
            {aboutList ?
                <>
                    <section className="about-banner">
                        <div className="base-container base-padding">
                            <h1>{t("mainHead2")}</h1>
                            <p>{props.language === "ru" ? aboutList.headRU : props.language === "kg" ? aboutList.headKG : aboutList.headEN}</p>
                        </div>
                    </section>
                    <section className="base-padding base-container">
                        <SanitizedHTML
                            allowedTags={[
                                "h2",
                                "h4",
                                "h5",
                                "h6",
                                "blockquote",
                                "p",
                                "a",
                                "ul",
                                "ol",
                                "nl",
                                "li",
                                "b",
                                "i",
                                "u",
                                "strong",
                                "em",
                                "strike",
                                "code",
                                "hr",
                                "br",
                                "div",
                                "table",
                                "thead",
                                "caption",
                                "tbody",
                                "tr",
                                "th",
                                "td",
                                "pre",
                                "img",
                                "video",
                                "source",
                                "span",
                                "iframe",
                                "sub",
                                "sup",
                                "span",
                            ]}
                            allowedAttributes={{
                                h2: ["style"],
                                h4: ["style"],
                                h5: ["style"],
                                h6: ["style"],
                                blockquote: ["style"],
                                p: ["style"],
                                a: ["style"],
                                ul: ["style"],
                                ol: ["style"],
                                nl: ["style"],
                                li: ["style"],
                                b: ["style"],
                                i: ["style"],
                                u: ["style"],
                                strong: ["style"],
                                em: ["style"],
                                strike: ["style"],
                                code: ["style"],
                                hr: ["style"],
                                br: ["style"],
                                div: ["style"],
                                table: ["style"],
                                thead: ["style"],
                                caption: ["style"],
                                tbody: ["style"],
                                tr: ["style"],
                                th: ["style"],
                                td: ["style"],
                                pre: ["style"],
                                img: ["style", "src"],
                                video: ["style"],
                                source: ["style"],
                                span: ["style"],
                                iframe: ["style"],
                                sub: ["style"],
                                sup: ["style"],
                            }}
                            html={props.language === "ru" ? aboutList.contentRU : props.language === "kg" ? aboutList.contentKG : aboutList.contentEN}
                        />
                    </section>
                </> : <></>
            }
        </div>
    );
}
