import React, {useState, useEffect} from "react";
import API from "../../API";
import "./index.css";

export default function PrePost(props) {
    const [testQuestions, setTestQuestions] = useState([]);
    const [isRegistered, setIsRegistered] = useState(!!window.localStorage.getItem("ARZ,pre,"))
    const [isPost, setIsPost] = useState(!!window.localStorage.getItem("ARZ,post,"))
    const [types, setTypes] = useState([])
    const [userInfo, setUserInfo] = useState({
        name: ""
    })
    const [testAnswers] = useState([])

    useEffect(() => {
        if (isRegistered) {
            if (isPost)
                API.getPostTestQuestions(props.match.params.testId, window.localStorage.getItem("ARZ,post,"))
                    .then(response => {
                        console.log(response.data)
                        setTestQuestions(response.data.test.questions)
                        setQuestionsTypes(response.data.test.questions)
                    })
                    .catch((error) => alert("–¢–µ—Å—Ç " + error))
            else
                API.getPreTestQuestions(props.match.params.testId, window.localStorage.getItem("ARZ,pre,"))
                    .then(response => {
                        console.log(response.data)
                        setTestQuestions(response.data.test.questions)
                        setQuestionsTypes(response.data.test.questions)
                    })
                    .catch((error) => alert("–¢–µ—Å—Ç " + error))
        }
        window.scrollTo(0, 0);
    }, [props.match.params.testId, isRegistered]);

    const registerOnTest = (e) => {
        e.preventDefault()
        API.postPrePostTestRegistration(props.match.params.testId, userInfo)
            .then((response) => {
                window.localStorage.setItem("ARZ,pre,", response.data._id)
                setIsRegistered(true)
            })
            .catch((error) => alert("–†–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—è " + error))
    }

    const setQuestionsTypes = (questions) => {
        let types = questions.map((item) => {
            if (!item.alternatives.length) {
                return "text"
            } else {
                let correctCount = item.alternatives.reduce((count, item) => {
                    return count + item.isCorrect
                }, 0)
                if (correctCount > 1) {
                    return "some"
                } else {
                    return "one"
                }
            }
        })
        setTypes(types)
    }

    const handleTestInput = (e) => {
        let a = e.target.id.split("$-$")
        if (e.target.type === "textarea") {
            testAnswers[e.target.name] = {
                question: a[0],
                text: e.target.value
            }
        }
        if (e.target.type === "radio") {
            testAnswers[e.target.name] = {
                question: a[0],
                isCorrect: a[1] === "true"
            }
        }
        if (e.target.type === "checkbox") {
            let temp = e.target.parentNode.parentNode.children;
            let answers = []
            for (let i in temp) {
                if (temp[i].className === "flex-row align-center") {
                    answers.push((temp[i].children[0].id.split('$-$')[1] === "true") === temp[i].children[0].checked)
                }
            }
            testAnswers[e.target.name] = {
                question: a[0],
                isCorrect: answers.every(item => item)
            }
        }
    }

    const postAnswers = (e) => {
        e.preventDefault();
        let body = {
            "userId": isPost ? window.localStorage.getItem("ARZ,post,") : window.localStorage.getItem("ARZ,pre,"),
            "answers": testAnswers
        }
        if (isPost)
            API.postPostTestAnswers(props.match.params.testId, body)
                .then(response => {
                    window.localStorage.removeItem("ARZ,pre,")
                    window.localStorage.removeItem("ARZ,post,")
                    alert("–°–ø–∞—Å–∏–±–æ –∑–∞ –æ—Ç–≤–µ—Ç—ã –Ω–∞ –ø–æ—Å—Ç —Ç–µ—Å—Ç")
                    window.location.reload()
                })
                .catch(error => console.log(error))
        else
            API.postPreTestAnswers(props.match.params.testId, body)
                .then(response => {
                    alert("–°–ø–∞—Å–∏–±–æ –∑–∞ –æ—Ç–≤–µ—Ç—ã –Ω–∞ –ø—Ä–µ —Ç–µ—Å—Ç")
                    API.postPostTestRegistration(props.match.params.testId, {
                        "userId": window.localStorage.getItem("ARZ,pre,")
                    })
                        .then(response => {
                            window.localStorage.setItem("ARZ,post,", response.data._id)
                            setIsPost(true)
                            window.location.reload()
                        })
                        .catch(e => console.log(e))
                })
                .catch(error => console.log(error))
        console.log(body)
    }

    return (
        <div className="pre-post-section base-padding base-container flex-col align-center">
            <h1>–ü—Ä–æ—Ö–æ–∂–¥–µ–Ω–∏–µ –ø—Ä–µ-–ø–æ—Å—Ç —Ç–µ—Å—Ç–∞</h1>
            {
                testQuestions && testQuestions.length ?
                    <form onSubmit={postAnswers} className="pre-post-test-container">
                        {
                            testQuestions.map((item, id) => (
                                <div style={{
                                    width: "100%"
                                }} key={id} className="flex-col align-start">
                                    <h3>{item.question}</h3>
                                    <div className="pre-post-test-alternatives">
                                        {types[id] === "one" ?
                                            item.alternatives.map((item2, id2) => (
                                                <div key={id2} className="flex-row align-center">
                                                    <input required
                                                           id={item.question + "$-$" + item2.isCorrect + "$-$" + id2}
                                                           type="radio" name={id} onChange={handleTestInput}/>
                                                    <label
                                                        htmlFor={item.question + "$-$" + item2.isCorrect + "$-$" + id2}
                                                        key={id2}>{item2.text}</label>
                                                </div>
                                            ))
                                            : types[id] === "text" ?
                                                <textarea rows="1" required
                                                          id={item.question + "$-$null$-$"}
                                                          name={id} onChange={handleTestInput}/>
                                                :
                                                item.alternatives.map((item2, id2) => (
                                                    <div key={id2} className="flex-row align-center">
                                                        <input
                                                            id={item.question + "$-$" + item2.isCorrect + "$-$" + id2}
                                                            type="checkbox" name={id} onChange={handleTestInput}/>
                                                        <label
                                                            htmlFor={item.question + "$-$" + item2.isCorrect + "$-$" + id2}
                                                            key={id2}>{item2.text}</label>
                                                    </div>
                                                ))
                                        }
                                    </div>
                                </div>
                            ))
                        }
                        <button type="submit">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>
                    </form>
                    :
                    <form className="flex-col align-center justify-center" onSubmit={registerOnTest}>
                        <div className="flex-col align-center justify-center">
                            <label htmlFor="name">–í–≤–µ–¥–∏—Ç–µ –≤–∞—à–µ –∏–º—è</label>
                            <input name="name" id="name" type="text"
                                   onChange={(e) => setUserInfo({name: e.target.value})}/>
                        </div>
                        <button type="submit">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</button>
                    </form>

            }
        </div>
    );
}
