import React, {useState, useEffect} from "react";
import {useTranslation} from 'react-i18next';
import API from "../../API";
import QuizCard from "../../components/quiz-card";
import quizBg from "../../images/bg.png";
import Loader from "react-loader-spinner";
import BlogBanner from "../../components/blog-banner";
import "./index.css";

export default function Quiz(props) {
    const {t, } = useTranslation();
    const [quizList, setQuizList] = useState([]);


    useEffect(() => {
        window.scrollTo(0, 0);

        const fetchData = async () => {
            const result = await API.getAllTests(props.language);
            setQuizList(result.data.tests);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    return (
        <>
            <BlogBanner
                blogBannerInfo={{
                    header: t("quizBannerHeader"),
                    backImg: quizBg,
                    backColor: "#F7C510",
                }}
            />
            <section>
                <div className="quiz-grid base-container base-padding grid-2">
                    {quizList ? (
                        quizList.map((item, id) => (
                            <QuizCard
                                key={id}
                                cardContent={{
                                    status: item.status,
                                    id: item._id,
                                    name: item.title,
                                }}
                            />
                        ))
                    ) : (
                        <Loader
                            type="TailSpin"
                            color="#00BFFF"
                            height={50}
                            width={50}
                            style={{margin: "auto"}}
                            timeout={5000}
                        />
                    )}
                </div>
            </section>
        </>
    );
}
