import React, { useState} from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Header from "./components/header";
import MainPage from "./containers/main-page";
import About from "./containers/about";
import SexBlog from "./containers/sex-blog";
import SexBlogArticle from "./containers/sex-blog-article";
import ParentsBlog from "./containers/parents-blog";
import ParentsBlogArticle from "./containers/parents-blog-article";
import Stories from "./containers/stories";
import StoriesArticle from "./containers/stories-article";
import Quiz from "./containers/quiz";
import QuizPage from "./containers/quiz-page";
import Faq from "./containers/faq";
import CoachAuthorization from "./containers/coach-autorization";
import CoachSection from "./containers/coach-section";
import Footer from "./components/footer";
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css"
import NotFound from "./containers/not-found";
import PrePost from "./containers/pre-post-test";

function App() {
    const [currentLanguage, selectLanguage] = useState(localStorage.getItem("currentARZLanguage") || "ru")

    const changeLanguage = (language) => {
        localStorage.setItem("currentARZLanguage", language)
        selectLanguage(language)
    }

    return (
        <Router>
            <Header language={currentLanguage} selectLanguage={changeLanguage}/>
            <Switch>
                <Route path="/" exact  render={(props) => <MainPage {...props} language={currentLanguage}/>}/>
                <Route path="/about" render={(props) => <About {...props} language={currentLanguage}/>}/>
                <Route path="/sexblog/:sexBlogArticleId" component={SexBlogArticle}/>
                <Route path="/sexblog" render={(props) => <SexBlog {...props} language={currentLanguage}/>}/>
                <Route
                    path="/parentsblog/:parentsBlogArticleId"
                    component={ParentsBlogArticle}
                />
                <Route path="/parentsblog" render={(props) => <ParentsBlog {...props} language={currentLanguage}/>}/>
                <Route path="/stories/:storyId" component={StoriesArticle}/>
                <Route path="/stories" render={(props) => <Stories {...props} language={currentLanguage}/>}/>
                <Route path="/quiz/:quizId" component={QuizPage}/>
                <Route path="/pre-post/:testId" component={PrePost}/>
                <Route path="/quiz" render={(props) => <Quiz {...props} language={currentLanguage}/>}/>
                <Route path="/faq" render={(props) => <Faq {...props} language={currentLanguage}/>}/>
                <Route path="/coach" render={(props) => <CoachSection {...props} language={currentLanguage}/>}/>
                <Route path="/coachlogin" component={CoachAuthorization}/>
                <Route exact component={NotFound}/>
            </Switch>
            <Footer language={currentLanguage}/>
        </Router>
    );
}

export default App;
