import React, {useState} from "react";
import {NavLink, Redirect} from "react-router-dom";
import {useTranslation} from 'react-i18next';

import "./index.css";

export default function CoachNavPanel() {
    const {t} = useTranslation();
    const [isUser, setIsUser] = useState(true);

    const userQuit = () => {
        window.localStorage.removeItem("user");
        setIsUser(false)
    }

    if (isUser)
        return (
            <nav className="coach-nav base-container flex-row justify-btw">
                <div className="flex-row justify-start">
                    <NavLink exact activeClassName="coach-active" to="/coach/tests">
                        {t("coachSectionTest")}
                    </NavLink>
                    <NavLink exact activeClassName="coach-active" to="/coach">
                        {t("coachSectionMaterials")}
                    </NavLink>
                    <NavLink exact activeClassName="coach-active" to="/coach/manual">
                        {t("coachSectionManual")}
                    </NavLink>
                    <NavLink exact activeClassName="coach-active" to="/coach/account">
                        {t("coachSectionAccount")}
                    </NavLink>
                </div>
                <button onClick={userQuit}>{t("coachSectionExit")}</button>
            </nav>
        );
    else return <Redirect push to="/coachlogin"/>;
}
