import React, {useState, useEffect} from "react";
// import AwesomeSlider from "react-awesome-slider";
// import "react-awesome-slider/dist/styles.css";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import {Carousel} from 'react-responsive-carousel';

import "./index.css";

export default function QuizContainer(props) {
    const questions = props.quizInfo;

    const [answersState, setAnswersState] = useState([]);
    const [isAnyAnswerGiven, setIsAnyAnswerGiven] = useState([]);

    function setStartAnswersState() {
        let startAnswersState = [];
        // eslint-disable-next-line
        for (let i in questions) {
            startAnswersState.push(false);
        }
        setAnswersState(startAnswersState);
        setIsAnyAnswerGiven(startAnswersState);
    }

    function setCurrentAnswersState(id, isRight) {
        let currentAnswersState = [];
        let currentIsAnyAnswersGivenState = [];
        for (let i in answersState) {
            // eslint-disable-next-line
            currentAnswersState.push(i == id ? isRight : answersState[i]);
            // eslint-disable-next-line
            currentIsAnyAnswersGivenState.push(i == id ? true : isAnyAnswerGiven[i]);
        }
        setIsAnyAnswerGiven(currentIsAnyAnswersGivenState);
        setAnswersState(currentAnswersState);
    }

    useEffect(() => {
        window.scrollTo(0, 0);
        setStartAnswersState();
        // eslint-disable-next-line
    }, [questions]);

    return (
        <div className="quiz-container flex-row justify-btw align-center">
            {answersState && (
                <Carousel>
                    {questions && questions.map((item, questionId) => (
                        <div key={questionId} className="quiz-block flex-col justify-btw align-center">
                            <h2>{item.question}</h2>
                            <div className="answers-container">
                                {item.alternatives.map((item, id) => (
                                    <div
                                        key={id}
                                        className="answer"
                                        onClick={() =>
                                            setCurrentAnswersState(questionId, item.isCorrect)
                                        }
                                        style={{
                                            color: `${
                                                answersState[questionId] && item.isCorrect
                                                    ? "#ffffff"
                                                    : "#000000"
                                            }`,
                                            background: `${
                                                answersState[questionId] && item.isCorrect
                                                    ? "#75BF43"
                                                    : "#f7c510"
                                            }`,
                                        }}
                                    >
                                        {item.text}
                                    </div>
                                ))}
                            </div>
                            <div
                                className="question-description"
                                style={{
                                    display: `${
                                        answersState[questionId] && isAnyAnswerGiven[questionId]
                                            ? "block"
                                            : "none"
                                    }`,
                                }}
                            >
                                {item.description}
                            </div>
                            <div
                                className="question-wrong-description"
                                style={{
                                    display: `${
                                        !answersState[questionId] && isAnyAnswerGiven[questionId]
                                            ? "block"
                                            : "none"
                                    }`,
                                }}
                            >
                                –ü–æ–ø—Ä–æ–±—É–π –µ—â–µ —Ä–∞–∑)
                            </div>
                        </div>
                    ))
                    }
                </Carousel>
            )}
        </div>
    );
}
