import React, {useEffect} from "react";
import TopBanner from "../../components/top-banner";
import ParentsBlogCardsSection from "../../components/parents-blog-cards-section";
import {useTranslation} from 'react-i18next';

import ParentsBlogImg from "../../images/parentsBlogImg.png";
import ParentsBlogBg from "../../images/parentsBlogBg.png";

import "./index.css";

export default function ParentsBlog(props) {
    const {t, } = useTranslation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    const topBannerInfo = {
        header: t("parentsBlogBannerHeader"),
        description: t("parentsBlogBannerDescription"),
        frontImg: ParentsBlogImg,
        backImg: ParentsBlogBg,
        backColor: "#F16822",
    };

    return (
        <>
            <TopBanner topBannerInfo={topBannerInfo}/>
            <section>
                <div className="parents-blog-cards base-container">
                    <ParentsBlogCardsSection language={props.language}/>
                </div>
            </section>
        </>
    );
}
