import React from "react";
import { Link } from "react-router-dom";

import "./index.css";

export default function CoachMaterialCard(props) {
  const { _id, title, content, photo } = props.cardContent;
  return (
    <Link
      to={`/coach/${_id}`}
      className="coach-material-card flex-row align-start justify-btw"
    >
      <img src={photo} alt={title} />
      <div>
        <h2>{title}</h2>
        <p>{content.replace(/(<([^>]+)>)/gi, "").replace(/&nbsp;/g, " ").substring(0,120) + "..."}</p>
      </div>
    </Link>
  );
}
