import React, {useEffect, useState} from "react";
import {PDFExport} from "@progress/kendo-react-pdf";
import "./index.css";
import API from "../../API";
import {useTranslation} from "react-i18next";

export default function CoachManual(props) {
    const [pdfExportRef, setPdfExportRef] = useState()
    const [coachManual, setCoachManual] = useState();
    const {t} = useTranslation()

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getCoachManual(props.language);
            setCoachManual(result.data.leaderships);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    const exportPDFWithComponent = () => {
        pdfExportRef.save();
    };
    return (
        <section className="manual-section bottom-padding base-container">
            {coachManual ? coachManual.map((item, id) => (
                <div key={id}>
                    <button className="k-button" onClick={exportPDFWithComponent}>
                        {t("downloadButton")}
                    </button>
                    <PDFExport
                        ref={component => setPdfExportRef(component)}
                        paperSize="A4"
                        scale={0.5}
                        margin={15}
                        fileName={`AIS –ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä ${new Date().getFullYear()}`}
                        author="AIS Home"
                    >
                        <h2>{item.title}</h2>
                        <h3>{item.text}</h3>
                    </PDFExport>
                </div>
            )) : <></>}
        </section>
    );
}
