import React, {useState} from "react";
import {useTranslation} from 'react-i18next';
import "./index.css";
import API from "../../API";

export default function CoachSessionForm() {
    const {t, } = useTranslation();

    const [trainingInfo, setTrainingInfo] = useState({
        "type": "basic",
        "place": null,
        "composition": "school",
        "total": 0,
        "male": 0,
        "female": 0,
        "noGender": 0,
        "arzMembers": 0,
        "date": null,
        "language": "ru",
        "userId": window.localStorage.getItem("user")
    })

    const handleInputs = (e) => {
        e.persist();
        setTrainingInfo({
            ...trainingInfo,
            [e.target.name]: e.target.value,
        });

    }

    const handleSubmit = (e) => {
        e.preventDefault();
        API.postSessionForm(trainingInfo)
            .then((response) => {
                alert("–û—Ç—á–µ—Ç —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–ª–µ–Ω")
            })
            .catch(error => {
                alert("–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è")
            })

    }

    return (
        <div className="coach-session-form flex-col align-start">
            <h2>{t("coachSectionStatFormData")}</h2>
            <form onSubmit={handleSubmit} className="grid-2">
                <div className="flex-col">
                    <label>{t("coachSectionType")}</label>
                    <select name="type" onChange={handleInputs}>
                        <option value="basic">{t("coachSectionType1")}</option>
                        <option value="sessions">{t("coachSectionType2")}</option>
                        <option value="shares">{t("coachSectionType3")}</option>
                    </select>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionDate")}</label>
                    <input name="date" type="date" onChange={handleInputs}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionPlace")}</label>
                    <input name="place" type="text" onChange={handleInputs}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionLanguages")}</label>
                    <select name="language" onChange={handleInputs}>
                        <option value="ru">{t("coachSectionLanguages1")}</option>
                        <option value="kg">{t("coachSectionLanguages2")}</option>
                        <option value="en">{t("coachSectionLanguages3")}</option>
                    </select>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionAll")}</label>
                    <input name="total" min={0} type="number" onChange={handleInputs}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionMan")}</label>
                    <input name="male" min={0} type="number" onChange={handleInputs}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionWoman")}</label>
                    <input name="female" min={0} type="number" onChange={handleInputs}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionGenderNeutral")}</label>
                    <input name="noGender" min={0} type="number" onChange={handleInputs}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionAlliance")}</label>
                    <input name="arzMembers" min={0} type="number" onChange={handleInputs}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionPeopleType")}</label>
                    <select name="composition" onChange={handleInputs}>
                        <option value="school">{t("coachSectionPeopleType1")}</option>
                        <option value="students">{t("coachSectionPeopleType2")}</option>
                        <option value="adults">{t("coachSectionPeopleType3")}</option>
                        <option value="mix">{t("coachSectionPeopleType4")}</option>
                    </select>
                </div>
                <button type="submit">{t("faqFormButton")}</button>
            </form>
        </div>
    );
}
