import React, {useEffect, useState} from "react";
import {useTranslation} from 'react-i18next';
import "./index.css";
import API from "../../API";
import Loader from "react-loader-spinner";

export default function CoachQuizContainer(props) {
    const {t,} = useTranslation();
    const [prePostTestInfo, setPrePostTestInfo] = useState()
    const [coachTest, setCoachTest] = useState()
    const [testRegisterInfo, setTestRegisterInfo] = useState({
        fio: "",
        sofio: "",
        data: "",
        place: "",
        count: "",
    })

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getPostTestsInfo(props.match.params.testId);
            setPrePostTestInfo(result.data)
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getCoachTests(window.localStorage.getItem("user"));
            setCoachTest(result.data)
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    const handleChange = (e) => {
        e.persist()
        setTestRegisterInfo({...testRegisterInfo, [e.target.name]: e.target.value})
        console.log(testRegisterInfo)
    }

    const handleSubmit = (e) => {
        e.preventDefault();
        API.postPrePostTestCopy(props.match.params.testId, {coachId: window.localStorage.getItem("user"), ...testRegisterInfo})
            .then((response) => {
                console.log(response.data)
                window.location.reload()
            })
            .catch(() => {
                alert("–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è")
            })
    }

    return (
        <section className="coach-test-container base-container bottom-padding flex-col justify-start align-start">
            {prePostTestInfo ? <>
                <h1>{prePostTestInfo.test.title}</h1>
                <p>{prePostTestInfo.test.description || "–ë–µ–∑ –æ–ø–∏—Å–∞–Ω–∏—è"}</p>
                {prePostTestInfo.admins.indexOf(window.localStorage.getItem("user")) === -1 ?
                    <form className="flex-col" onSubmit={handleSubmit}>
                        <div className="form-container grid-2">
                            <div className="flex-col ">
                                <label>{t("coachSectionTestName")}</label>
                                <input required name="fio" type="text" onChange={handleChange}/>
                            </div>
                            <div className="flex-col ">
                                <label>{t("coachSectionTestSoName")}</label>
                                <input name="sofio" type="text" onChange={handleChange}/>
                            </div>
                            <div className="flex-col ">
                                <label>{t("coachSectionTestDate")}</label>
                                <input required name="data" type="date" onChange={handleChange}/>
                            </div>
                            <div className="flex-col ">
                                <label>{t("coachSectionTestPlace")}</label>
                                <input required name="place" type="text" onChange={handleChange}/>
                            </div>
                            <div className="flex-col ">
                                <label>{t("coachSectionTestCount")}</label>
                                <input required name="count" type="number" onChange={handleChange}/>
                            </div>
                        </div>
                        <button type="submit">{t("coachSectionTestButton")}</button>
                    </form>
                    :
                    <>
                        {coachTest ?
                            <div className="flex-col align-start justify-start">
                                <h2>–í–∞—à–∞ —Å—Å—ã–ª–∫–∞</h2>
                                <a href={`http://localhost:3000/pre-post/${coachTest.filter((item) => {
                                    return item.test._id === prePostTestInfo.test._id
                                })[0]._id}`}>
                                    http://localhost:3000/pre-post/{coachTest.filter((item) => {
                                    return item.test._id === prePostTestInfo.test._id
                                })[0]._id}
                                </a>
                            </div>
                            :
                            <></>
                        }
                    </>
                }
            </> : <Loader
                type="TailSpin"
                color="#00BFFF"
                height={50}
                width={50}
                style={{margin: "auto"}}
                timeout={5000}
            />
            }
        </section>
    );
}
