import React, {useState, useEffect} from "react";
import API from "../../API";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faPlus, faMinus} from "@fortawesome/free-solid-svg-icons";
import Loader from "react-loader-spinner";
import "./index.css";
import SanitizedHTML from "react-sanitized-html";

export default function FaqAnswered(props) {
    const [isOpened, setIsOpened] = useState([]);

    const [faqList, setFaqList] = useState([]);

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getAllFaq(props.language);
            setFaqList(result.data);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    function setStartState() {
        let startState = [];
        // eslint-disable-next-line
        for (let i in faqList) {
            startState.push(false);
        }
        setIsOpened(startState);
    }

    function switchState(id) {
        let currentState = [];
        for (let i in faqList) {
            // eslint-disable-next-line
            currentState.push(id == i ? !isOpened[i] : false);
        }
        setIsOpened(currentState);
    }

    useEffect(() => {
        setStartState();
        // eslint-disable-next-line
    }, []);

    return (
        <div className="faq-container flex-col justify-start align-start">
            {faqList.length ? (
                faqList.map((item, id) => (
                    <div key={id} className="faq-container-item">
                        <div className="faq-container-question flex-row justify-btw align-center">
                            <p>{item.question}</p>
                            <div
                                className="flex-row justify-center align-center"
                                onClick={() => switchState(id)}
                                style={{
                                    background: isOpened[id] ? "#007D7F" : "#FFFFFF",
                                    color: isOpened[id] ? "#FFFFFF" : "#007D7F",
                                    border: isOpened[id] ? "none" : "2px solid #007D7F",
                                }}
                            >
                                <FontAwesomeIcon
                                    style={{display: isOpened[id] ? "none" : "block"}}
                                    icon={faPlus}
                                />
                                <FontAwesomeIcon
                                    style={{display: isOpened[id] ? "block" : "none"}}
                                    icon={faMinus}
                                />
                            </div>
                        </div>
                        <div
                            style={{display: isOpened[id] ? "block" : "none"}}
                            className="faq-container-answer"
                        >
                            <SanitizedHTML
                                allowedTags={[
                                    "h2",
                                    "h4",
                                    "h5",
                                    "h6",
                                    "blockquote",
                                    "p",
                                    "a",
                                    "ul",
                                    "ol",
                                    "nl",
                                    "li",
                                    "b",
                                    "i",
                                    "u",
                                    "strong",
                                    "em",
                                    "strike",
                                    "code",
                                    "hr",
                                    "br",
                                    "div",
                                    "table",
                                    "thead",
                                    "caption",
                                    "tbody",
                                    "tr",
                                    "th",
                                    "td",
                                    "pre",
                                    "img",
                                    "video",
                                    "source",
                                    "span",
                                    "iframe",
                                    "sub",
                                    "sup",
                                    "span",
                                ]}
                                allowedAttributes={{
                                    h2: ["style"],
                                    h4: ["style"],
                                    h5: ["style"],
                                    h6: ["style"],
                                    blockquote: ["style"],
                                    p: ["style"],
                                    a: ["style"],
                                    ul: ["style"],
                                    ol: ["style"],
                                    nl: ["style"],
                                    li: ["style"],
                                    b: ["style"],
                                    i: ["style"],
                                    u: ["style"],
                                    strong: ["style"],
                                    em: ["style"],
                                    strike: ["style"],
                                    code: ["style"],
                                    hr: ["style"],
                                    br: ["style"],
                                    div: ["style"],
                                    table: ["style"],
                                    thead: ["style"],
                                    caption: ["style"],
                                    tbody: ["style"],
                                    tr: ["style"],
                                    th: ["style"],
                                    td: ["style"],
                                    pre: ["style"],
                                    img: ["style", "src"],
                                    video: ["style"],
                                    source: ["style"],
                                    span: ["style"],
                                    iframe: ["style"],
                                    sub: ["style"],
                                    sup: ["style"],
                                }}
                                html={item.answer}
                            />
                        </div>
                    </div>
                ))
            ) : (
                <Loader
                    type="TailSpin"
                    color="#00BFFF"
                    height={50}
                    width={50}
                    style={{margin: "auto"}}
                    timeout={5000}
                />
            )}
        </div>
    );
}
