import React, {useState} from "react";
import {NavLink, Link} from "react-router-dom";
import logo from "../../images/logo.png";
import {useTranslation} from 'react-i18next';
import "./index.css";

export default function Header(props) {
    const {t, i18n} = useTranslation();
    const [isOpen, setIsOpen] = useState(false);

    return (
        <header
            style={{
                height: window.innerWidth > 900 ? "auto" : isOpen ? "340px" : "80px",
                transition: "all 0.2s ease-in-out",
            }}
        >
            <div className="base-container flex-row align-center justify-btw">
                <div className="logo-burger flex-row align-center justify-btw">
                    <Link to="/">
                        <img alt="–ê–†–ó" src={logo}/>
                    </Link>
                    <div className="burger-container" onClick={() => setIsOpen(!isOpen)}>
                        <div
                            className="burger"
                            style={{
                                transform: isOpen ? "rotate(45deg)" : "none",
                                boxShadow: isOpen
                                    ? "none"
                                    : "0 6px 0 #1f1e16, 0 -6px 0 #1f1e16",
                            }}
                        >
                            <div
                                className="x-line"
                                style={{
                                    display: isOpen ? "block" : "none",
                                }}
                            />
                        </div>
                    </div>
                </div>
                <div
                    className="header-content flex-row justify-end align-center"
                    style={{
                        clipPath:
                            window.innerWidth > 900
                                ? "polygon(0 0, 100% 0, 100% 100%, 0 100%)"
                                : isOpen
                                ? "polygon(0 0, 100% 0, 100% 100%, 0 100%)"
                                : "polygon(0 0, 100% 0, 100% 0, 0 0)",
                    }}
                >
                    <nav className="flex-row justify-btw">
                        <NavLink onClick={() => setIsOpen(!isOpen)} activeClassName="active-about" to="/about">
                            {t("headerAbout")}
                        </NavLink>
                        <NavLink onClick={() => setIsOpen(!isOpen)} activeClassName="active-sexblog" to="/sexblog">
                            {t("headerSexblog")}
                        </NavLink>
                        <NavLink onClick={() => setIsOpen(!isOpen)} activeClassName="active-quiz" to="/quiz">
                            {t("headerQuiz")}
                        </NavLink>
                        <NavLink onClick={() => setIsOpen(!isOpen)} activeClassName="active-parentsblog"
                                 to="/parentsBlog">
                            {t("headerParentsblog")}
                        </NavLink>
                        <NavLink onClick={() => setIsOpen(!isOpen)} activeClassName="active-stories" to="/stories">
                            {t("headerStories")}
                        </NavLink>
                        <NavLink onClick={() => setIsOpen(!isOpen)} activeClassName="active-faq" to="/faq">
                            {t("headerFaq")}
                        </NavLink>
                        <NavLink onClick={() => setIsOpen(!isOpen)} activeClassName="active-coach" to="/coachlogin">
                            {t("headerCoach")}
                        </NavLink>
                    </nav>
                    <div className="flex-row localization">
                        <div className={props.language === "ru" ? "active" : ""}
                             onClick={() => {
                                 props.selectLanguage("ru");
                                 i18n.changeLanguage("ru");
                             }}>RU
                        </div>
                        <div className="slash">|</div>
                        <div className={props.language === "kg" ? "active" : ""}
                             onClick={() => {
                                 props.selectLanguage("kg");
                                 i18n.changeLanguage("kg");
                             }}>KG
                        </div>
                        <div className="slash">|</div>
                        <div className={props.language === "en" ? "active" : ""}
                             onClick={() => {
                                 props.selectLanguage("en");
                                 i18n.changeLanguage("en");
                             }}>EN
                        </div>
                    </div>
                </div>
            </div>
        </header>
    );
}
