import Axios from "axios";

const http = Axios.create({
    baseURL: "https://sexedu.rhak.kg/api/",
    headers: {"Content-Type": "application/json"},
});

export default {
    getCoachInfo: (id) => http.get(`user/${id}`),
    getCoachSessions: (id) => http.get(`training/${id}`),
    getMainPageInfo: () => http.get("banner/"),
    getParentsBlogAllArticles: (language) => http.get(`${language}/article/forParents`),
    getSexBlogAllArticles: (language) => http.get(`${language}/article/aboutSex`),
    getStoryBlogAllArticles: (language) => http.get(`${language}/article/history`),
    getAllCoachMaterials: (language) => http.get(`${language}/article/materials`),
    getAllTags: (language) => http.get(`${language}/tag`),
    getAllArticlesByTag: (tagId) => http.get(`tag/${tagId}`),
    getArticleByKey: (key) => http.get(`blog/${key}`),
    getAboutUsInfo: () => http.get(`aboutUs`),
    getAllTests: (language) => http.get(`${language}/test`),
    getTestByKey: (key) => http.get(`/test/${key}`),
    getAllFaq: (language) => http.get(`${language}/faq`),
    getCoachManual: (language) => http.get(`${language}/lead`),
    getAllPrePostTests: (language) => http.get(`${language}/pre-post-test`),
    getPostTestsInfo: (id) => http.get(`/pre-post-test/one/${id}`),
    getCoachTests: (id) => http.get(`pre-post-test-get-admin-tests/${id}`),
    getPreTestQuestions: (id, body) => http.get(`pre-test/${id}&${body}`),
    getPostTestQuestions: (id, body) => http.get(`post-test/${id}&${body}`),
    getPrePostTestResults: (id) => http.get(`pre-post-test-calculate/${id}`),
    postPrePostTestRegistration: (id, body) => http.post(`pre-test-registration/${id}`, body),
    postPostTestRegistration: (id, body) => http.post(`post-test-authorization/${id}`, body),
    postPrePostTestCopy: (id, body) => http.post(`pre-post-test-create-copy/${id}`, body),
    postPreTestAnswers: (id, body) => http.post(`pre-test/${id}`, body),
    postPostTestAnswers: (id, body) => http.post(`post-test/${id}`, body),
    postSessionForm: (body) => http.post("training", body),
    postUserInfoChange: (id, body) => http.post(`user/edit/${id}`, body, {
        headers: null
    }),
    postFaqQuestion: (body) => http.post("send", body),
    postLogin: (body) => http.post("login", body),
};
