import React, {useEffect, useState} from "react";
import {useTranslation} from 'react-i18next';
import "./index.css";
import API from "../../API";

export default function CoachResultTable() {
    const {t} = useTranslation();
    const [coachTests, setCoachTests] = useState()
    const [questionsCount, setQuestionsCount] = useState()
    const [preTestResults, setPreTestResults] = useState()
    const [postTestResults, setPostTestResults] = useState()

    useEffect(() => {
        const fetchData = async () => {
            API.getCoachTests(window.localStorage.getItem("user"))
                .then((response) => {
                    console.log(response.data)
                    setCoachTests(response.data)
                    if (response.data[0]) {
                        setPreTestResults(response.data[0].preTestUsers)
                        setPostTestResults(response.data[0].postTestUsers)
                        setQuestionsCount(response.data[0].test.questions)
                    }
                })
        };
        fetchData();
        // eslint-disable-next-line
    }, []);
    return (
        <div className="coach-result-table flex-col align-start">
            <h2>{t("coachSectionSessionData")}</h2>
            {coachTests && coachTests.length ?
                <>
                    <h4>{t("coachSectionPreTestResult")}</h4>
                    <div className="table-container">
                        <table>
                            <thead>
                            <tr>
                                <th>‚Ññ</th>
                                <th>{t("coachSectionSessionName")}</th>
                                {questionsCount ? questionsCount.map((item, id) => (
                                    <th key={id}>#{id + 1}</th>
                                )) : <></>}
                            </tr>
                            </thead>
                            <tbody>
                            {
                                preTestResults ? preTestResults.map((item, id) => (
                                    <tr key={id}>
                                        <td>{id + 1}</td>
                                        <td>{item.username}</td>
                                        {
                                            item.answers.map((item, id) => (
                                                <td key={id}>{item.text || (item.isCorre—Åt ? "–í–µ—Ä–Ω–æ" : "–ù–µ–≤–µ—Ä–Ω–æ")}</td>
                                            ))
                                        }
                                    </tr>
                                )) : <></>
                            }
                            </tbody>
                        </table>
                    </div>
                    <h4>{t("coachSectionPostTestResult")}</h4>
                    <div className="table-container">
                        <table>
                            <thead>
                            <tr>
                                <th>‚Ññ</th>
                                <th>{t("coachSectionSessionName")}</th>
                                {questionsCount ? questionsCount.map((item, id) => (
                                    <th key={id}>#{id + 1}</th>
                                )) : <></>}
                            </tr>
                            </thead>
                            <tbody>
                            {
                                postTestResults ? postTestResults.map((item, id) => (
                                    <tr key={id}>
                                        <td>{id + 1}</td>
                                        <td>{item.username}</td>
                                        {
                                            item.answers.map((item, id) => (
                                                <td key={id}>{item.text || item.isCorre—Åt ? "–í–µ—Ä–Ω–æ" : "–ù–µ–≤–µ—Ä–Ω–æ"}</td>
                                            ))
                                        }
                                    </tr>
                                )) : <></>
                            }
                            </tbody>
                        </table>
                    </div>
                </> : <h2>–ù–µ—Ç –¥–∞–Ω–Ω—ã—Ö</h2>
            }
        </div>
    );
}
