import React, {useState} from "react";
import {useTranslation} from 'react-i18next';
import "./index.css";
import API from "../../API";

export default function FaqForm() {
    const {t, } = useTranslation();
    const [formData, setFormData] = useState({
        name: "",
        phone: "",
        text: "",
    })

    const handleSubmit = (e) => {
        e.preventDefault();
        if (checkForm()) {
            API.postFaqQuestion(formData)
                .then(r => {
                    alert("–í–∞—à –≤–æ–ø—Ä–æ—Å –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω!")
                    resetForm()
                })
                .catch(e => console.log(e));
        } else {
            alert("–ó–∞–ø–æ–ª–Ω–∏—Ç–µ –≤—Å–µ –ø–æ–ª—è")
        }
    }

    const handleUserInput = (e) => {
        setFormData({...formData, [e.target.name]: e.target.value});
    }

    const resetForm = () => {
        document.getElementsByTagName("form")[0].reset()
    }

    const checkForm = () => {
        return formData.name !== "" && formData.phone !== "" && formData.text !== "";
    }

    return (
        <form
            id="contact-form"
            onSubmit={handleSubmit}
            method="POST"
            className="faq-form flex-col justify-start align-start"
        >
            <div className="flex-col justify-start align-start">
                <label htmlFor="name">{t("faqForm1")}</label>
                <input
                    type="text"
                    placeholder={t("faqForm1Placeholder")}
                    name="name"
                    id="name"
                    onChange={handleUserInput}
                />
            </div>
            <div className="flex-col justify-start align-start">
                <label htmlFor="phone">{t("faqForm2")}</label>
                <input
                    type="text"
                    placeholder="+996 999 999 999"
                    name="phone"
                    id="phone"
                    onChange={handleUserInput}
                />
            </div>
            <div className="flex-col justify-start align-start">
                <label htmlFor="text">{t("faqForm3")}</label>
                <textarea
                    type="text"
                    name="text"
                    placeholder={t("faqForm3Placeholder")}
                    id="text"
                    onChange={handleUserInput}
                />
            </div>
            <button type="submit" className="flex-col justify-center align-center ">
                {t("faqFormButton")}
            </button>
        </form>
    );
}