import React from "react";
import SexBlogBigCard from "../sex-blog-big-card";
import SexBlogSmallCard from "../sex-blog-small-card";
import {useTranslation} from 'react-i18next';

import "./index.css";

export default function FirstArticles(props) {
    const {t, } = useTranslation();

    return (
        <section className="first-articles-section">
            <div className="base-container base-padding flex-col align-start justify-btw">
                <h1>{t("mainArticles")}</h1>
                <div className="first-articles-grid grid-3">
                    {props.articles.map((item, id) =>
                        id % 4 ? (
                            <SexBlogSmallCard key={id} cardContent={{
                                id: item._id,
                                name: item.title,
                                description: item.content,
                                img: item.photo,
                            }}/>
                        ) : (
                            <SexBlogBigCard key={id} cardContent={{
                                id: item._id,
                                name: item.title,
                                description: item.content,
                                img: item.photo,
                            }}/>
                        )
                    )}
                </div>
            </div>
        </section>
    );
}
