import React, {useState, useEffect} from "react";
import API from "../../API";
import Loader from "react-loader-spinner";
import StoryCard from "../story-card/index";

import "./index.css";

export default function StoryCardsSection(props) {
    const [storyBlogArticles, setStoryBlogArticles] = useState({});

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getStoryBlogAllArticles(props.language);
            setStoryBlogArticles(result.data.blogs);
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    return (
        <div className="story-cards-section base-padding">
            {storyBlogArticles.length ? (
                storyBlogArticles.map((item, id) => (
                    <StoryCard
                        key={id}
                        cardContent={{
                            id: item._id,
                            name: item.title,
                            description: item.content,
                            img: item.photo,
                        }}
                    />
                ))
            ) : (
                <Loader
                    type="TailSpin"
                    color="#00BFFF"
                    height={50}
                    width={50}
                    style={{margin: "auto"}}
                    timeout={5000}
                />
            )}
        </div>
    );
}
