import React from "react";
import {Link} from "react-router-dom"
import faqMan from "../../images/faq-man.png"
import {useTranslation} from 'react-i18next';

import "./index.css";

export default function FaqSection() {
    const {t} = useTranslation();

    return (
        <section className="faq-section">
            <div className="base-container flex-row align-center justify-btw">
                <div className="flex-col">
                    <h1>{t("mainFaq1")}</h1>
                    <h1>{t("mainFaq2")}</h1>
                    <Link to="/faq" className="flex-row align-center justify-center">{t("mainFaqButton")}</Link>
                </div>
                <img src={faqMan} alt="faq"/>
            </div>
        </section>
    );
}
