import React, {useEffect, useState} from "react";
import QuizCard from "../../components/quiz-card";
import "./index.css";
import API from "../../API";
import Loader from "react-loader-spinner";

export default function CoachTests(props) {
    const [prePostTestList, setPrePostTestList] = useState()

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getAllPrePostTests(props.language);
            setPrePostTestList(result.data.filter((item) => {
                return item.test
            }));
        };
        fetchData();
        // eslint-disable-next-line
    }, [props.language]);

    return (
        <section className="coach-test-grid bottom-padding grid-2 base-container">
            {prePostTestList ?
                prePostTestList.map((item, id) => (
                    <QuizCard key={id}
                              cardContent={{status: false, id: item._id, name: item.test.title}}
                    />
                ))
                : <Loader
                    type="TailSpin"
                    color="#00BFFF"
                    height={50}
                    width={50}
                    style={{margin: "auto"}}
                    timeout={5000}
                />
            }
        </section>
    );
}
