import React, {useEffect, useState} from "react";
import {useTranslation} from 'react-i18next';
import Avatar from "../../images/avatar.jpg"
import "./index.css";
import {Switch, Route, NavLink} from "react-router-dom";
import CoachResultTable from "../../components/coach-result-table";
import CoachSessionForm from "../../components/coach-session-form";
import CoachPersonalForm from "../../components/coach-personal-form";
import API from "../../API";


export default function CoachAccount() {
    const {t,} = useTranslation();
    const [coachData, setCoachData] = useState({})
    const [coachSessions, setCoachSessions] = useState()

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getCoachInfo(window.localStorage.getItem("user"));
            setCoachData(result.data.user);
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getCoachSessions(window.localStorage.getItem("user"));
            setCoachSessions(result.data.trainings);
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    return (
        <section className="coach-account-section base-container bottom-padding flex-row align-start justify-btw">
            {coachData.fio ?
                <div className="coach-main-info flex-col align-center">
                    <div className="flex-col align-center justify-center">
                        <img
                            src={coachData.avatar || Avatar}
                            alt="arz"
                        />
                        <h1>{coachData.fio}</h1>
                    </div>
                    <div>
                        <div className="flex-col align-start justify-btw">
                            <h5>{t("coachSectionWork")}</h5>
                            <p>{coachData.workplace}</p>
                        </div>
                        <div className="flex-col align-start justify-btw">
                            <h5>{t("coachSectionPhone")}</h5>
                            <p>{coachData.phone}</p>
                        </div>
                        <div className="flex-col align-start justify-btw">
                            <h5>Email</h5>
                            <p>{coachData.email}</p>
                        </div>
                        <div className="flex-col align-start justify-btw">
                            <h5>{t("coachSectionBranch")}</h5>
                            <p>{coachData.filial}</p>
                        </div>
                        <div className="flex-col align-start justify-btw">
                            <h5>{t("coachSectionLanguages")}</h5>
                            <p>{coachData.language}</p>
                        </div>
                    </div>
                </div>
                :
                <></>}
            <div className="stats-and-links flex-col align-start justify-center">
                <div className="coach-stats-info flex-row">
                    <div className="flex-row align-center">
                        <h2 className="flex-row align-center justify-center">
                            {coachSessions ? coachSessions.filter(item => item.type === "sessions").length : 0}
                        </h2>
                        <p>{t("coachSectionSession")}</p>
                    </div>
                    <div className="flex-row align-center">
                        <h2 className="flex-row align-center justify-center">
                            {coachSessions ? coachSessions.length : 0}
                        </h2>
                        <p>{t("coachSectionTrainings")}</p>
                    </div>
                    <div className="flex-row align-center">
                        <h2 className="flex-row align-center justify-center">
                            {coachSessions ? coachSessions.reduce((sum, item) => {
                                return sum + item.total
                            }, 0) : 0}
                        </h2>
                        <p>{t("coachSectionPeople")}</p>
                    </div>
                </div>
                <div className="coach-forms-links flex-row">
                    <NavLink activeClassName="form-link-active" exact
                             to="/coach/account">{t("coachSectionResults")}</NavLink>
                    <NavLink activeClassName="form-link-active" exact
                             to="/coach/account/session">{t("coachSectionStatForm")}</NavLink>
                    <NavLink activeClassName="form-link-active" exact
                             to="/coach/account/personal">{t("coachSectionPersonal")}</NavLink>
                </div>
                <Switch>
                    <Route exact path="/coach/account" component={CoachResultTable}/>
                    <Route exact path="/coach/account/session" component={CoachSessionForm}/>
                    <Route exact path="/coach/account/personal">
                        <CoachPersonalForm coachData={coachData}/>
                    </Route>
                </Switch>
            </div>
        </section>
    );
}
