import React, {useEffect, useState} from "react";
import {useTranslation} from 'react-i18next';
import Select from 'react-select';
import "./index.css";
import API from "../../API";
import Axios from "axios";

export default function CoachPersonalForm() {
    const {t} = useTranslation();
    const [ava, setAva] = useState()
    const [formData, setFormData] = useState({
        workplace: "",
        language: "",
        old_phone: "",
        phone: "",
        email: "",
        avatar: "",
        old_password: "",
        new_password: "",
    })
    const [defaultLanguages, setDefaultLanguages] = useState()

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getCoachInfo(window.localStorage.getItem("user"));
            console.log(result.data.user)
            let languagesArray = result.data.user.language.split(",");
            let tempLang = [];
            for (let i in languagesArray) {
                tempLang.push({value: languagesArray[i], label: languagesArray[i]});
            }
            setDefaultLanguages(tempLang);
            setStartValues(result.data.user, languagesArray);
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    const setStartValues = (data, languages) => {
        let temp = {
            workplace: data.workplace || "",
            language: languages.join(",") || "",
            old_phone: data.phone || "",
            phone: data.phone || "",
            email: data.email || "",
            avatar: "",
            old_password: window.localStorage.getItem("currentARZUserPassword") || "",
            new_password: "",
        }
        setFormData(temp)
    }

    const handleInput = (e) => {
        if (!e.target)
            setFormData({
                ...formData, language: e.map((item) => {
                    return item.value
                }).join(",")
            })
        else if (e.target.name === "avatar") {
            setFormData({...formData, [e.target.name]: e.target.files[0]})
        } else
            setFormData({...formData, [e.target.name]: e.target.value})
        console.log(formData)
    }

    const handleFileInputs = (files) => {
        setAva(files[0]);
    }

    const handleSubmit = (e) => {
        e.preventDefault()
        let body = new FormData()
        console.log(ava)
        body.append("workplace", formData.workplace)
        body.append("language", formData.language)
        body.append("old_phone", formData.old_phone)
        body.append("phone", formData.phone)
        body.append("email", formData.email)
        body.append("avatar", ava)
        body.append("old_password", formData.old_password)
        body.append("new_password", formData.new_password)

        Axios.post("https://sexedu.rhak.kg/api/user/edit/" + window.localStorage.getItem('user'), body, {headers: {'Content-Type': 'multipart/form-data'}})
            .then(() => {
                alert("–í–∞—à–∏ –¥–∞–Ω–Ω—ã–µ —É—Å–ø–µ—à–Ω–æ –∏–∑–º–µ–Ω–µ–Ω—ã")
                window.location.reload()
            })
            .catch(() => alert("–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ"))

        // API.postUserInfoChange(window.localStorage.getItem("user"), formData)
        //     .then(() => {
        //         alert("–í–∞—à–∏ –¥–∞–Ω–Ω—ã–µ —É—Å–ø–µ—à–Ω–æ –∏–∑–º–µ–Ω–µ–Ω—ã")
        //         window.location.reload()
        //     })
        //     .catch(() => alert("–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫! –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ"))
    }

    return (
        <div className="coach-personal-form flex-col align-start">
            <h2>{t("coachSectionPersonalData")}</h2>
            <form className="grid-2" onSubmit={handleSubmit}>
                <div className="flex-col">
                    <label>{t("coachSectionWork")}</label>
                    <input name="workplace" type="text" onChange={handleInput} defaultValue={formData.workplace}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionPhone")}</label>
                    <input name="phone" type="text" onChange={handleInput} defaultValue={formData.phone}/>
                </div>
                <div className="flex-col">
                    <label>Email</label>
                    <input name="email" type="text" onChange={handleInput} defaultValue={formData.email}/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionLanguages")}</label>
                    {defaultLanguages ?
                        <Select
                            defaultValue={defaultLanguages}
                            closeMenuOnSelect={false}
                            isMulti
                            name="language"
                            options={[
                                {value: 'ru', label: 'ru'},
                                {value: 'kg', label: 'kg'},
                                {value: 'en', label: 'en'},
                            ]}
                            onChange={handleInput}
                        /> : <></>}
                </div>
                <div className="flex-col">
                    <label>{t("coachAuthLabel2")}</label>
                    <input name="new_password" type="text" id="avatar" onChange={handleInput}
                           placeholder="***********"/>
                </div>
                <div className="flex-col">
                    <label>{t("coachSectionImage")}</label>
                    <input name="avatar" type="file" onChange={e => handleFileInputs(e.target.files)}/>
                </div>
                <button type="submit">{t("coachSectionPersonalButton")}</button>
            </form>
        </div>
    );
}
