import React, {useEffect, useState} from "react";
import {PDFExport} from "@progress/kendo-react-pdf";
import SanitizedHTML from "react-sanitized-html";
import AwesomeSlider from "react-awesome-slider";
import "react-awesome-slider/dist/styles.css";
import BlogBanner from "../../components/blog-banner";

import "./index.css";
import API from "../../API";
import {useTranslation} from "react-i18next";

export default function CoachMaterialArticle(props) {
    const {t} = useTranslation()
    const [pdfExportRef, setPdfExportRef] = useState()
    const [articleInfo, setArticleInfo] = useState([]);
    useEffect(() => {
        window.scrollTo(0, 0);

        const fetchData = async () => {
            const result = await API.getArticleByKey(
                props.match.params.materialId
            );
            setArticleInfo(result.data.blog);
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    const exportPDFWithComponent = () => {
        pdfExportRef.save();
    };

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    return (
        <>
            <div className="download-container">
                <div className="base-container flex justify-end">
                    <button className="k-button" onClick={exportPDFWithComponent}>
                        {t("downloadButton")}
                    </button>
                </div>
            </div>
            <PDFExport
                ref={component => setPdfExportRef(component)}
                paperSize="A4"
                scale={0.5}
                margin={0}
                fileName={`AIS –ö–∞–ª—å–∫—É–ª—è—Ç–æ—Ä ${new Date().getFullYear()}`}
                author="AIS Home"
            >
                <BlogBanner
                    blogBannerInfo={{
                        header: articleInfo.title,
                        backImg: "none",
                        backColor: "#9B0A00",
                    }}
                />
                <div className="base-container base-padding blog-description">
                    <div>

                        <SanitizedHTML
                            allowedTags={[
                                "h2",
                                "h4",
                                "h5",
                                "h6",
                                "blockquote",
                                "p",
                                "a",
                                "ul",
                                "ol",
                                "nl",
                                "li",
                                "b",
                                "i",
                                "u",
                                "strong",
                                "em",
                                "strike",
                                "code",
                                "hr",
                                "br",
                                "div",
                                "table",
                                "thead",
                                "caption",
                                "tbody",
                                "tr",
                                "th",
                                "td",
                                "pre",
                                "img",
                                "video",
                                "source",
                                "span",
                                "iframe",
                                "sub",
                                "sup",
                            ]}
                            allowedAttributes={{
                                h2: ["style"],
                                h4: ["style"],
                                h5: ["style"],
                                h6: ["style"],
                                blockquote: ["style"],
                                p: ["style"],
                                a: ["style"],
                                ul: ["style"],
                                ol: ["style"],
                                nl: ["style"],
                                li: ["style"],
                                b: ["style"],
                                i: ["style"],
                                u: ["style"],
                                strong: ["style"],
                                em: ["style"],
                                strike: ["style"],
                                code: ["style"],
                                hr: ["style"],
                                br: ["style"],
                                div: ["style"],
                                table: ["style"],
                                thead: ["style"],
                                caption: ["style"],
                                tbody: ["style"],
                                tr: ["style"],
                                th: ["style"],
                                td: ["style"],
                                pre: ["style"],
                                img: ["style", "src"],
                                video: ["style"],
                                source: ["style"],
                                span: ["style"],
                                iframe: ["style"],
                                sub: ["style"],
                                sup: ["style"],
                            }}
                            html={articleInfo.content}
                        />
                    </div>
                    {articleInfo.slider && (
                        <AwesomeSlider infinite={true} bullets={false}>
                            {articleInfo.slider.map((item, id) => (
                                <div key={id}>
                                    <img src={item} alt={id}/>
                                </div>
                            ))}
                        </AwesomeSlider>
                    )}
                </div>
            </PDFExport>
        </>
    );
}
