import React, {useEffect} from "react";
import {useTranslation} from 'react-i18next';
import BlogBanner from "../../components/blog-banner";
import FaqAnswered from "../../components/faq-answered";
import FaqForm from "../../components/faq-form";
import quizBg from "../../images/bg.png";
import "./index.css";

export default function Faq(props) {
    const {t, } = useTranslation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    return (
        <section>
            <BlogBanner
                blogBannerInfo={{
                    header: t("faqBannerHeader"),
                    backImg: quizBg,
                    backColor: "#32BEBF",
                }}
            />

            <div className="faq-page-container base-container base-padding flex-row justify-btw align-start">
                <FaqAnswered language={props.language}/>
                <FaqForm />
            </div>
        </section>
    );
}
