import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import FirstArticles from "../../components/first-articles";
import FaqSection from "../../components/faq-section";
import QuizContainer from "../../components/quiz-container";
import {useTranslation} from 'react-i18next';
import main from "../../images/main.jpg";
import "./index.css";
import API from "../../API";

export default function MainPage(props) {
    const {t,} = useTranslation();

    const [mainPageInfo, setMainPageInfo] = useState({})

    useEffect(() => {
        const fetchData = async () => {
            const result = await API.getMainPageInfo();
            setMainPageInfo(result.data.banner);
        };
        fetchData();
        // eslint-disable-next-line
    }, []);

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    return (
        <>
            {
                mainPageInfo ?
                    <>
                        <section className="main-banner">
                            <div className="main-banner-text base-padding flex-row align-center justify-btw">
                                <div className="flex-col align-start justify-center">
                                    <h2>{t("mainHead1")}</h2>
                                    <h1>{t("mainHead2")}</h1>
                                    {mainPageInfo.content ?
                                        <p>{props.language === "ru" ?
                                            mainPageInfo.content :
                                            props.language === "en" ?
                                                mainPageInfo.contentEN :
                                                mainPageInfo.contentKG}</p> : <></>}
                                    <Link className="flex-row align-center justify-center" to="/about">
                                        {t("mainHeadButton")}
                                    </Link>
                                </div>
                            </div>
                            <img src={main} alt="–ê–†–ó"/>
                        </section>
                        {
                            props.language === "ru" ?
                                mainPageInfo.homeBlogs ?
                                    <FirstArticles articles={mainPageInfo.homeBlogs}/> :
                                    <></> :
                                props.language === "en" ?
                                    mainPageInfo.blogsEN ?
                                        <FirstArticles articles={mainPageInfo.blogsEN}/> :
                                        <></> :
                                    mainPageInfo.blogsKG ?
                                        <FirstArticles articles={mainPageInfo.blogsKG}/> :
                                        <></>
                        }
                        <div className="main-page-quiz-container">
                            <div className="base-container base-padding flex-col justify-center align-center">
                                {
                                    props.language === "ru" ?
                                        mainPageInfo.homeTest ?
                                            <QuizContainer quizInfo={mainPageInfo.homeTest.questions}/> :
                                            <></> :
                                        props.language === "en" ?
                                            mainPageInfo.testEN ?
                                                <QuizContainer quizInfo={mainPageInfo.testEN.questions}/> :
                                                <></> :
                                            mainPageInfo.testKG ?
                                                <QuizContainer quizInfo={mainPageInfo.testKG.questions}/> :
                                                <></>
                                }
                            </div>
                        </div>
                        <FaqSection/>
                    </> : <></>
            }
        </>
    );
}
